import {
  AfterViewInit,
  Component,
  Input,
  OnInit,
  ViewChild,
} from "@angular/core";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTable, MatTableDataSource } from "@angular/material/table";

@Component({
  selector: "app-preview-attachments",
  templateUrl: "./preview-attachments.component.html",
  styleUrls: ["./preview-attachments.component.scss"],
})
export class PreviewAttachmentsComponent implements OnInit, AfterViewInit {
  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: false }) sort: MatSort;
  @ViewChild(MatTable, { static: false }) table: MatTable<object>;
  @Input() previewAttachmentsList = [];
  @Input() dataSource: MatTableDataSource<object>;
  @Input() displayedColumns = "";

  constructor() {}

  ngOnInit(): void {}

  ngAfterViewInit(): void {
    console.log(this.dataSource);
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }
  }

  viewFormOrMaterial(attachmentUrl) {
    if (attachmentUrl) {
      window.open(attachmentUrl);
    }
  }
}
